import React from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyNumber, WindowComponent } from 'summer';
import { CLASS_NAME, Rubrica } from '../../../../models/dtos/mfp/rubrica';

interface RubricaHomolognetProps extends EditViewProps<Rubrica> {}

const RubricaHomolognetEditView: React.FC<RubricaHomolognetProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<Rubrica>({
    defaultValues: selectedItem,
    mode: 'onBlur',
  });

  const validateExtras = formValues => {
    const erros = [];
    if (!formValues.valor && !formValues.quantidade && !formValues.percentual && !formValues.vrBaseCalculo) {
      erros.push("Pelo menos uma das opções devem ser marcadas: 'Valor', 'Quantidade', 'Percentual' ou 'Base de Cálculo'.");
    }
    return erros;
  };

  return (
    <WindowComponent title="Configuração de Rubrica">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} validateExtras={validateExtras}>
        <Row>
          <Col>
            <InputGroupInline
              id="codigo"
              inputProps={true}
              label="Código"
              formProps={{ ...formProps, validation: onlyNumber(1, 8) }}
              isPrependLabel={true}
              type={InputType.NUMBER}
              placeholder="Informe o código"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="descricao"
              label="Descrição"
              formProps={{ ...formProps, validation: { required: true, maxLength: 200 } }}
              isPrependLabel={true}
              placeholder="Informe a descrição da Rubrica"
            />
          </Col>
        </Row>
        <Row className="mb-5">
          <Col>
            <CustomInput type="switch" label="Valor" name="valor" id="valor" innerRef={formProps.register} />
          </Col>
          <Col>
            <CustomInput type="switch" label="Quantidade" name="quantidade" id="quantidade" innerRef={formProps.register} />
          </Col>
          <Col>
            <CustomInput type="switch" label="Percentual" name="percentual" id="percentual" innerRef={formProps.register} />
          </Col>
          <Col>
            <CustomInput type="switch" label="Base de Cálculo" name="vrBaseCalculo" id="vrBaseCalculo" innerRef={formProps.register} />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default RubricaHomolognetEditView;
